export const HomeScreen: React.FC = () => {
  const [channels, setChannels] = useState<ChatChannel[]>(CHAT_CHANNELS);
  const [searchText, setSearchText] = useState('');
  const [agentChatVisible, setAgentChatVisible] = useState(false);
  const [selectedAgent, setSelectedAgent] = useState<AgentType>('xiaoai');
  const [contactsVisible, setContactsVisible] = useState(false);
  const [accessibilitySettingsVisible, setAccessibilitySettingsVisible] = useState(false);
  const [accessibilityEnabled, setAccessibilityEnabled] = useState(false);
  const [showNavigationTest, setShowNavigationTest] = useState(false);

  // ËøáÊª§ËÅäÂ§©È¢ëÈÅì
  const filteredChannels = channels.filter(channel => {
    const matchesSearch = channel.name.toLowerCase().includes(searchText.toLowerCase()) ||
                         channel.lastMessage.toLowerCase().includes(searchText.toLowerCase());
    return matchesSearch;
  });

  // ÊâìÂºÄËÅäÂ§©
  const openChat = (channel: ChatChannel) => {
    if (channel.type === 'agent') {
      Alert.alert(
        `‰∏é${channel.name}ÂØπËØù`,
        `${channel.specialization}\n\nÂç≥Â∞ÜËøõÂÖ•‰∏é${channel.name}ÁöÑÂØπËØùÁïåÈù¢`,
        [
          { text: 'ÂèñÊ∂à', style: 'cancel' },
          { text: 'ÂºÄÂßãÂØπËØù', onPress: () => startAgentChat(channel) }
        ]
      );
    } else {
      Alert.alert('ËÅäÂ§©ÂäüËÉΩ', `Âç≥Â∞ÜÊâìÂºÄ‰∏é${channel.name}ÁöÑËÅäÂ§©`);
    }
  };

  // ÂºÄÂßãÊô∫ËÉΩ‰ΩìÂØπËØù
  const startAgentChat = async (channel: ChatChannel) => {
    try {
      console.log(`ü§ñ ÂêØÂä®‰∏é${channel.name}ÁöÑÂØπËØù...`);
      
      // Ê∏ÖÈô§Êú™ËØªÊ∂àÊÅØ
      setChannels(prev => prev.map(ch => 
        ch.id === channel.id ? { ...ch, unreadCount: 0 } : ch
      ));

      // ËÆæÁΩÆÈÄâ‰∏≠ÁöÑÊô∫ËÉΩ‰ΩìÂπ∂ÊòæÁ§∫ÂØπËØùÁïåÈù¢
      if (channel.agentType) {
        setSelectedAgent(channel.agentType);
        setAgentChatVisible(true);
        console.log(`ü§ñ ËøõÂÖ•${channel.name}ÂØπËØùÁïåÈù¢`);
      }

    } catch (error) {
      console.error('ÂêØÂä®Êô∫ËÉΩ‰ΩìÂØπËØùÂ§±Ë¥•:', error);
      Alert.alert('ËøûÊé•Â§±Ë¥•', `Êó†Ê≥ïËøûÊé•Âà∞${channel.name}ÊúçÂä°ÔºåËØ∑Á®çÂêéÈáçËØï`);
    }
  };

  // Â§ÑÁêÜËÅîÁ≥ª‰∫∫ÁÇπÂáª
  const handleContactPress = (contact: Contact) => {
    if (contact.type === 'agent' && contact.agentType) {
      setSelectedAgent(contact.agentType);
      setAgentChatVisible(true);
      setContactsVisible(false);
    } else {
      Alert.alert('ËÅäÂ§©ÂäüËÉΩ', `Âç≥Â∞ÜÊâìÂºÄ‰∏é${contact.name}ÁöÑËÅäÂ§©`);
    }
  };

  // Ê∏≤ÊüìËÅäÂ§©È¢ëÈÅìÈ°π
  const renderChannelItem = ({ item }: { item: ChatChannel }) => {
    const getChannelColor = () => {
      switch (item.type) {
        case 'agent':
          return colors.primary;
        case 'doctor':
          return '#34C759';
        case 'group':
          return '#FF9500';
        default:
          return colors.textSecondary;
      }
    };

    return (
      <TouchableOpacity style={styles.channelItem} onPress={() => openChat(item)}>
        <View style={styles.avatarContainer}>
          <Text style={styles.avatar}>{item.avatar}</Text>
          {item.isOnline && <View style={styles.onlineIndicator} />}
        </View>
        
        <View style={styles.channelContent}>
          <View style={styles.channelHeader}>
            <Text style={styles.channelName}>{item.name}</Text>
            <Text style={styles.messageTime}>{item.lastMessageTime}</Text>
          </View>
          
          <View style={styles.channelFooter}>
            <Text style={styles.lastMessage} numberOfLines={1}>
              {item.lastMessage}
            </Text>
            {item.unreadCount > 0 && (
              <View style={styles.unreadBadge}>
                <Text style={styles.unreadText}>
                  {item.unreadCount > 99 ? '99+' : item.unreadCount}
                </Text>
              </View>
            )}
          </View>
          
          <Text style={[styles.specialization, { color: getChannelColor() }]}>
            {item.specialization}
          </Text>
        </View>
      </TouchableOpacity>
    );
  };

  return (
    <SafeAreaView style={styles.container}>
        <TouchableOpacity
          style={{
            position: 'absolute',
            top: 100,
            right: 20,
            backgroundColor: '#FF9500',
            padding: 10,
            borderRadius: 5,
            zIndex: 1000,
          }}
          onPress={() => setShowNavigationTest(!showNavigationTest)}
        >
          <Text style={{ color: 'white', fontSize: 12 }}>ÂØºËà™ÊµãËØï</Text>
        </TouchableOpacity>
        
        {showNavigationTest && (
          <Modal
            visible={showNavigationTest}
            animationType="slide"
            presentationStyle="pageSheet"
          >
            <NavigationTest />
            <TouchableOpacity
              style={{
                position: 'absolute',
                top: 50,
                right: 20,
                backgroundColor: '#FF3B30',
                padding: 10,
                borderRadius: 5,
              }}
              onPress={() => setShowNavigationTest(false)}
            >
              <Text style={{ color: 'white' }}>ÂÖ≥Èó≠</Text>
            </TouchableOpacity>
          </Modal>
        )}
      {/* Â§¥ÈÉ® */}
      <View style={styles.header}>
        <Text style={styles.title}>ËÅäÂ§©</Text>
        <View style={styles.headerActions}>
          <TouchableOpacity 
            style={styles.headerButton}
            onPress={() => setAccessibilitySettingsVisible(true)}
          >
            <Icon 
              name="account-voice" 
              size={24} 
              color={accessibilityEnabled ? colors.success : colors.textSecondary} 
            />
          </TouchableOpacity>
          <TouchableOpacity 
            style={styles.headerButton}
            onPress={() => setContactsVisible(true)}
          >
            <Icon name="account-multiple" size={24} color={colors.primary} />
          </TouchableOpacity>
          <TouchableOpacity style={styles.headerButton}>
            <Icon name="plus" size={24} color={colors.primary} />
          </TouchableOpacity>
        </View>
      </View>

      {/* ÊêúÁ¥¢Ê°Ü */}
      <View style={styles.searchContainer}>
        <Icon name="magnify" size={20} color={colors.textSecondary} />
        <TextInput
          style={styles.searchInput}
          placeholder="ÊêúÁ¥¢ËÅäÂ§©ËÆ∞ÂΩï..."
          value={searchText}
          onChangeText={setSearchText}
          placeholderTextColor={colors.textSecondary}
        />
      </View>

      {/* ËÅäÂ§©È¢ëÈÅìÂàóË°® */}
      <FlatList
        data={filteredChannels}
        keyExtractor={item => item.id}
        renderItem={renderChannelItem}
        style={styles.channelsList}
        showsVerticalScrollIndicator={false}
        ItemSeparatorComponent={() => <View style={styles.separator} />}
        ListEmptyComponent={() => (
          <View style={styles.emptyState}>
            <Icon name="message-outline" size={64} color={colors.textSecondary} />
            <Text style={styles.emptyTitle}>ÊöÇÊó†ËÅäÂ§©ËÆ∞ÂΩï</Text>
            <Text style={styles.emptySubtitle}>ÁÇπÂáªÂè≥‰∏äËßíËÅîÁ≥ª‰∫∫ÂõæÊ†áÂºÄÂßãËÅäÂ§©</Text>
          </View>
        )}
      />

      {/* Âø´ÈÄüÊìç‰ΩúÊåâÈíÆ */}
      <TouchableOpacity style={styles.fabButton}>
        <Icon name="message-plus" size={24} color="white" />
      </TouchableOpacity>

      {/* Êô∫ËÉΩ‰ΩìÂØπËØùÁïåÈù¢ */}
      <AgentChatInterface
        visible={agentChatVisible}
        onClose={() => setAgentChatVisible(false)}
        agentType={selectedAgent}
        userId="current_user_id"
        accessibilityEnabled={accessibilityEnabled}
      />

      {/* ËÅîÁ≥ª‰∫∫ÂàóË°®Ê®°ÊÄÅÊ°Ü */}
      <Modal
        visible={contactsVisible}
        animationType="slide"
        presentationStyle="pageSheet"
        onRequestClose={() => setContactsVisible(false)}
      >
        <SafeAreaView style={styles.modalContainer}>
          <View style={styles.modalHeader}>
            <Text style={styles.modalTitle}>ËÅîÁ≥ª‰∫∫</Text>
            <TouchableOpacity
              onPress={() => setContactsVisible(false)}
              style={styles.modalCloseButton}
            >
              <Icon name="close" size={24} color={colors.text} />
            </TouchableOpacity>
          </View>
          <ContactsList
            contacts={CONTACTS}
            onContactPress={handleContactPress}
            showSearch={true}
            groupByType={true}
            showOnlineStatus={true}
          />
        </SafeAreaView>
      </Modal>

      {/* Êó†ÈöúÁ¢çËÆæÁΩÆÊ®°ÊÄÅÊ°Ü */}
      <AccessibilitySettings
        visible={accessibilitySettingsVisible}
        onClose={() => setAccessibilitySettingsVisible(false)}
        userId="current_user_id"
        onSettingsChange={setAccessibilityEnabled}
      />
    </SafeAreaView>
  );
};

